












import Vue from 'vue'
export default Vue.extend({
  data () {
    return { k: '', v: '' }
  },
  methods: {
    answerCheck (): void {
      const { state, commit } = this.$store
      const { num, answer } = state.fieldState
      const [k, v] = answer.split(' / ')
      if (k === this.k && v === this.v) {
        commit('listFilter', answer)
        commit('answerCheck', ~~num)
      } else {
        commit('setMsg', '틀렸습니다.')
      }
    },
  },
})
